.App {
  text-align: center;
  flex: 1 1 auto;     /* 1 */
  position: relative;
  left:0;
  right:0;
  min-width: 1px;
  max-width: 30em;
  margin: 0 auto;
  z-index: 1;
  width: 100%;        /* 2 */
  height: 90vh;
}

.Header {
  background-color: lightskyblue;
  height:60px;
  font-size: 50px;
  padding-bottom:0.1em;
}

.InvalidWordModal {
  background-color: lightblue;
  border: black solid 1px;
  border-radius: 0.4em;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%,-50%);
  font-size: 1.5em;
  height: 2em;
  line-height: 2em;
  padding-left: 1em;
  padding-right: 1em;
}

.SuccessModal {
  background-color: lightgreen;
  border: black solid 1px;
  border-radius: 0.4em;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%,-50%);
  font-size: 1.5em;
  height: 2em;
  line-height: 2em;
  padding-left: 1em;
  padding-right: 1em;
  width:10em;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.Keyboard {
  position: absolute;
  bottom: 1em;
  width: 100%;
  margin-left:auto;
  margin-right:auto;
}